    goToQuickQA () {
      if (this.power <= 0) {
        alert('請補充能力值')
        return
      }
      this.quickQA++
    },
    rightQA () {
      if (this.power <= 0) {
        alert('請補充能力值')
        return
      }
      alert('答對了！')
      this.quickQA++
    },
    wrongQA () {
      if (this.power <= 0) {
        alert('請補充能力值')
        return
      }
      alert('答錯了！')
      this.$store.commit('changePower', 1)
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.power = this.power
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
      if (this.power <= 0) {
        this.quickQA = 0
        this.step = 9
      }
      if (this.quickQA === 10) {
        this.step = 10
      }
      this.quickQA++
    },
    lastRightQA () {
      this.step = 10
    },
    goToStepQ911 () {
      this.step = 9.11
    },
    goToStepQ91 () {
      this.step = 9.1
    },
    goToStepQ11 () {
      this.step = 11
